/*--------------------------------------------------------------
  # Navigation Menu
  --------------------------------------------------------------*/
/* Desktop Navigation */
.nav-menu {
  padding: 70px 0 0 0;
}

.nav-menu * {
  list-style: none;
}

.nav-menu > ul > li {
  position: relative;
  white-space: nowrap;
}

.nav-menu a,
.nav-menu a:focus {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: #a8a9b4;
  padding: 12px 15px;
  margin-bottom: 13px;
  transition: 0.3s;
  font-size: 15px;
}

.nav-menu a i,
.nav-menu a:focus i {
  font-size: 24px;
  padding-right: 8px;
  color: #6f7180;
}

.nav-menu a:hover,
.nav-menu .active,
.nav-menu .active:focus,
.nav-menu li:hover > a {
  text-decoration: none;
  color: #fff;
}

.navbar .nav-menu {
  padding-top: 30px;
}

.nav-link-home {
  margin-left: 10px;
}

.link-name {
  margin-left: 10px;
}

/* Mobile Navigation */

.mobile-nav-toggle {
  position: fixed;
  right: 15px;
  top: 15px;
  z-index: 9998;
  border: 0;
  transition: all 0.4s;
  background-color: #149ddd;
  color: #fff;
  width: 40px;
  height: 40px;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  line-height: 0;
  border-radius: 50px;
}

.mobile-nav-active #header {
  left: 0;
}

.mobile-nav-active {
  overflow: hidden;
}

@media (min-width: 1024px) {
  .mobile-nav-toggle {
    display: none;
  }
}
